import { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import { YEAR_ANIMATION_DURATION } from '@/constants/';
/**
 *     (fromYearRef  toYearRef)  startYear  endYear.
 * @param fromYearRef -    .timeline-block__year       .
 * @param toYearRef -    .timeline-block__year        .
 * @param startYear -    .
 * @param endYear -    .
 * @returns - {void}
 */
export const useYearTweens = ({ fromYearRef, toYearRef, startYear, endYear }) => {
    //      
    const fromValue = useRef(null);
    const toValue = useRef(null);
    useEffect(() => {
        const fromEl = fromYearRef.current;
        const toEl = toYearRef.current;
        if (!fromEl || !toEl)
            return;
        /**
         *       .
         * @param el -  span  .
         * @param fallback -   .
         * @param ref -     .
         * @returns -   ,   ,    ,    .
         */
        const getStartValue = (el, fallback, ref) => {
            if (typeof ref.current === 'number')
                return ref.current;
            const parsed = Number(el.textContent);
            return Number.isNaN(parsed) ? fallback : parsed;
        };
        const fromState = { value: getStartValue(fromEl, startYear, fromValue) };
        const toState = { value: getStartValue(toEl, endYear, toValue) };
        //     fromYear
        const syncFrom = () => {
            fromValue.current = fromState.value;
            fromEl.textContent = Math.round(fromState.value).toString();
        };
        //     toYear
        const syncTo = () => {
            toValue.current = toState.value;
            toEl.textContent = Math.round(toState.value).toString();
        };
        //  fromYear
        const fromTween = gsap.to(fromState, {
            value: startYear,
            duration: YEAR_ANIMATION_DURATION,
            ease: 'power2.out',
            onUpdate: syncFrom
        });
        //  toYear
        const toTween = gsap.to(toState, {
            value: endYear,
            duration: YEAR_ANIMATION_DURATION,
            ease: 'power2.out',
            onUpdate: syncTo
        });
        //  
        return () => {
            fromTween.kill();
            toTween.kill();
        };
    }, [fromYearRef, toYearRef, startYear, endYear]);
};
export default useYearTweens;
